import { createContext, useCallback, useContext, useEffect, useMemo, useRef, useState } from "react"
import { useLocation, useNavigate } from "react-router-dom"
import { useSearchParams } from "react-router-dom"
import { useUser } from "@clerk/clerk-react"
import { DatasetScope } from "@rp/common/api-types"
import { chatDatasets, isDatasetScope } from "@rp/react/common/chat"
import { chatCustomerIo } from "@rp/react/common/customer-io"
import {
  LlmTimings,
  SummaryState,
  isChatContext,
  isChatFileContext,
  isChatWatchlistContext,
  isChatWorkflowContext,
  isHomeChatContext,
  isPromptLibraryContext,
} from "@rp/react/common/hooks"
import { useAccount } from "@rp/react/common/providers"
import { generatePromptFromWorkflow } from "@rp/react/common/workflow"
import {
  isUserInteraction,
  useChat,
  useCreateChat,
  useOptimisticChat,
} from "@rp/react/tanstack-api/chat"
import { useFilesToCustomerIO, useMultipleFiles } from "@rp/react/tanstack-api/file"
import {
  EngineChartContent,
  LLMFollowUpMessage,
  LlmTextContent,
} from "@rp/react/tanstack-api/llm-summary"
import { useWatchlist, useWatchlistsToCustomerIO } from "@rp/react/tanstack-api/watchlist"
import { CHAT_ROUTE } from "@rp/react/web/router"
import { RegenerateHandler } from "../PersistedChat/Chat"
import {
  AddPromptParams,
  ChatDataContextValue,
  CurrentInteraction,
  Props,
} from "./ChatDataProvider.types"
import { getInteractionOrigin } from "./getInteractionOrigin"

const FILES_PARAM = "files"
const SOURCES_PARAM = "source"
const WATCHLIST_PARAM = "watchlist"

const ChatDataContext = createContext<ChatDataContextValue | null>(null)

const ChatDataProvider = ({ children, id, onChatCreated, origin, enabledSources }: Props) => {
  // DATA
  const navigate = useNavigate()
  const { user } = useUser()
  const { hasSomeEntitlement } = useAccount()
  const [searchParams, setSearchParams] = useSearchParams()
  const { state } = useLocation()

  const context = useRef(state)
  const hasContext = isChatContext(context.current)
  const isComingFromPromptLib = useRef(false)
  const isComingFromWorkflow = useRef(false)

  const { data: chat, isError, isLoading, lastWatchlistId } = useChat(id)
  const { mutateAsync } = useCreateChat()

  // FILTERS: SCOPE
  const scopeParam = Object.values(chatDatasets).find(
    dataset => dataset.handle === searchParams.get(SOURCES_PARAM),
  )?.id
  const baseScopeWithoutEntitlements = isDatasetScope(scopeParam) ? scopeParam : undefined
  const baseScope =
    baseScopeWithoutEntitlements !== undefined &&
    chatDatasets[baseScopeWithoutEntitlements].entitlements !== undefined &&
    !hasSomeEntitlement(chatDatasets[baseScopeWithoutEntitlements].entitlements ?? [])
      ? undefined
      : baseScopeWithoutEntitlements
  const [scope, setScope] = useState<DatasetScope | undefined>(baseScope)

  // FILTERS: WATCHLISTs
  const baseWatchlist = searchParams.get(WATCHLIST_PARAM) ?? undefined
  const hasWatchlistContext = isChatWatchlistContext(context.current)
  //WORKFLOWS CONTEXT STATE
  const hasWorkflowContext = isChatWorkflowContext(context.current)

  const [watchlistId, setWatchlistId] = useState<string | undefined>(
    hasWatchlistContext ? context.current.watchlistIds[0] : baseWatchlist,
  )
  const { isError: isErrorWatchlist } = useWatchlist(watchlistId)
  const { watchlists } = useWatchlistsToCustomerIO(watchlistId)

  // FILTERS: FILES
  const baseFiles = searchParams.get(FILES_PARAM)?.split(",") ?? []
  const hasFileContext = isChatFileContext(context.current)
  const [filesIds, setFilesIds] = useState<Array<string>>(
    hasFileContext ? context.current.filesIds : baseFiles,
  )
  const allSelectedFiles = useMultipleFiles(filesIds)
  const realFileIds = useMemo(() => {
    return allSelectedFiles
      .filter(obj => !obj.isError && !obj.isLoading)
      .map(file => file.data?.id ?? "")
  }, [allSelectedFiles])
  const { files } = useFilesToCustomerIO(filesIds)
  const cIoScope = realFileIds.length > 0 ? "files" : scope ?? null

  // UI STATE
  const hasRestoredChat = useRef("")
  const [timingsMap, setTimingsMap] = useState<Record<string, LlmTimings>>({})
  const [isAuditMode, setIsAuditMode] = useState(true)
  const [followUpMap, setFollowUpMap] = useState<
    Record<string, LLMFollowUpMessage["followUp"]["queries"] | undefined>
  >({})

  const { addInteraction, removeLastInteraction } = useOptimisticChat()
  const [currentInteraction, setCurrentInteraction] = useState<undefined | CurrentInteraction>(
    undefined,
  )

  const removeFollowUpFromInteraction = useCallback((interactionId: string) => {
    setFollowUpMap(prev => ({ ...prev, [interactionId]: undefined }))
  }, [])

  // UTILS
  const setUrlParams = (paramName: string, value: string | undefined) => {
    if (id) {
      return
    }

    setSearchParams(
      prev => {
        value === undefined ? prev.delete(paramName) : prev.set(paramName, value)

        return prev
      },
      { replace: true },
    )
  }

  const resetChatState = useCallback(
    ({ withNavigation = true, resetWatchlistId = true } = {}) => {
      setIsAuditMode(true)
      setScope(undefined)
      hasRestoredChat.current = ""
      context.current = null
      if (withNavigation) {
        navigate(CHAT_ROUTE, { replace: true })
      }
      setCurrentInteraction(undefined)
      setFilesIds([])
      if (resetWatchlistId) {
        setWatchlistId(undefined)
      }
      setTimingsMap({})
      setFollowUpMap({})
      isComingFromPromptLib.current = false
      isComingFromWorkflow.current = false
    },
    [navigate],
  )

  const addPrompt = useCallback(
    async ({
      prompt,
      customTitle,
      replaceState = false,
      clickedPrompt,
      defaultMessage = false,
      customerIoScope = cIoScope,
      followUpSuggestionIndex,
      entities,
      interactionType,
      displayedMessage,
      watchlistId,
      forceNewChatId,
      workflow,
      watchlistInfo,
    }: AddPromptParams) => {
      const index = chat?.interactions.length ?? 0

      const iteration =
        currentInteraction !== undefined &&
        currentInteraction.stopped &&
        currentInteraction.userInput === prompt
          ? currentInteraction.iteration + 1
          : 0

      const userInteraction = "user_message"
      const defaultInteractionType = defaultMessage ? "empty_message" : userInteraction

      const finalOrigin =
        origin !== "chat"
          ? origin
          : getInteractionOrigin(
              context.current,
              chat?.interactions?.[0]?.origin?.type ?? defaultInteractionType,
            )

      setCurrentInteraction({
        userInput: prompt,
        error: false,
        stopped: false,
        edit: false,
        customTitle,
        iteration,
        index: chat ? chat.interactions.length : 0,
        interactionType: interactionType ?? defaultInteractionType,
        entities,
        workflow: workflow ?? context.current?.workflow,
        displayedMessage,
      })

      let chatId = forceNewChatId ? null : id
      if (!chatId) {
        const data = await mutateAsync({
          name: customTitle ?? prompt,
          scopeWatchlistId: watchlistId,
        })
        chatId = data.id
        onChatCreated?.(data.id, replaceState || hasWatchlistContext || hasFileContext)
      }
      chatCustomerIo.inputPromptSubmit({
        chatId,
        origin: finalOrigin,
        index,
        scope: customerIoScope,
        promptTopics: clickedPrompt?.relatedTopics ?? null,
        promptTask: clickedPrompt?.icon.label ?? null,
        files,
        watchlists,
        followUpSuggestionIndex: followUpSuggestionIndex ?? null,
        watchlistPrompt: {
          clickedSuggestedPromptText: watchlistInfo?.clickedSuggestedPromptText ?? null,
          suggestedPromptClassification: watchlistInfo?.suggestedPromptClassification ?? null,
          promptContextLevel: watchlistInfo?.promptContextLevel ?? null,
          promptCategory: watchlistInfo?.promptCategory ?? null,
          promptCompanyType: watchlistInfo?.promptCompanyType ?? null,
          dataViewAtClick: watchlistInfo?.dataViewAtClick ?? null,
        },
      })
    },

    [
      cIoScope,
      chat,
      currentInteraction,
      origin,
      id,
      files,
      watchlists,
      mutateAsync,
      onChatCreated,
      hasWatchlistContext,
      hasFileContext,
    ],
  )

  const addResponse = useCallback(
    (interactionId: string, data: SummaryState) => {
      if (!id) {
        return
      }

      setCurrentInteraction(undefined)

      addInteraction(id, {
        auditTraces: data.auditTraces,
        inputMessage: currentInteraction?.displayedMessage ?? currentInteraction?.userInput ?? "",
        interactionTimestamp: interactionId,
        responseBlock: data.responseBlock ?? {
          type: "TEXT",
          value: data.response,
        },
        responseBlocks: data.responseBlocks.filter(
          (block): block is LlmTextContent | EngineChartContent =>
            block.type === "TEXT" || block.type === "ENGINE",
        ),
        origin: { type: data.interactionType },
        scope: scope,
        scopeDocuments: realFileIds.map(fileId => ({
          id: fileId,
          fileName: "",
          uploadTs: "",
          rawSize: 0,
        })),
        scopeWatchlistId: watchlistId,
        dateCreated: "",
        lastUpdated: "",
        originSources: data.originSources,
        originSourcesMetadata: data.metadata,
        auditBlocks: data.auditBlocks,
      })

      setTimingsMap(prev => ({
        ...prev,
        [interactionId]: { ...data.timings, profiling: data.profiling },
      }))

      setFollowUpMap(prev => ({
        ...prev,
        [interactionId]: data.followUpQuestions,
      }))
    },
    [
      id,
      addInteraction,
      currentInteraction?.displayedMessage,
      currentInteraction?.userInput,
      scope,
      realFileIds,
      watchlistId,
    ],
  )

  const regenerateLastResponse: RegenerateHandler = useCallback(
    ({ isCurrentInteraction = false }) => {
      if (!id) {
        return
      }

      if (isCurrentInteraction) {
        setCurrentInteraction(prev => {
          chatCustomerIo.regenerateLastQuestionClick({
            chatId: id,
            index: currentInteraction?.index ?? 0,
            scope: realFileIds.length > 0 ? "files" : scope ?? null,
            files,
            watchlists,
          })
          return prev === undefined
            ? prev
            : { ...prev, stopped: false, error: false, edit: false, iteration: prev.iteration + 1 }
        })
        return
      }

      const regeneratedInteraction = removeLastInteraction(id)
      setCurrentInteraction({
        userInput: regeneratedInteraction?.inputMessage ?? "Error",
        stopped: false,
        error: false,
        edit: true,
        iteration: 0,
        index: chat ? chat.interactions.length - 1 : 0,
        interactionType: regeneratedInteraction?.origin.type ?? "user_message",
      })
      chatCustomerIo.regenerateLastQuestionClick({
        chatId: id,
        index: currentInteraction?.index ?? 0,
        scope: realFileIds.length > 0 ? "files" : scope ?? null,
        files,
        watchlists,
      })
    },
    [
      id,
      chat,
      removeLastInteraction,
      currentInteraction?.index,
      realFileIds.length,
      scope,
      files,
      watchlists,
    ],
  )

  const editLastQuestion = useCallback(
    (prompt: string) => {
      if (!id) {
        return
      }

      chatCustomerIo.editLastQuestionClick({
        chatId: id,
        index: currentInteraction?.index ?? 0,
        scope: realFileIds.length > 0 ? "files" : scope ?? null,
        files,
        watchlists,
      })

      removeLastInteraction(id)

      setCurrentInteraction({
        userInput: prompt,
        stopped: false,
        error: false,
        edit: true,
        iteration: 0,
        index: chat ? chat.interactions.length - 1 : 0,
        // If the user edits the prompt it can't be longer default message
        interactionType: "user_message",
      })
    },
    [
      id,
      currentInteraction?.index,
      realFileIds.length,
      scope,
      files,
      watchlists,
      removeLastInteraction,
      chat,
    ],
  )

  // EFFECTS
  useEffect(() => {
    if (isErrorWatchlist) {
      setWatchlistId(undefined)
    }
  }, [isErrorWatchlist])

  useEffect(() => {
    if (chat && chat.interactions.length > 0 && hasRestoredChat.current !== chat.id) {
      hasRestoredChat.current = chat.id
      const lastInteraction = chat.interactions[chat.interactions.length - 1]
      if (isUserInteraction(lastInteraction)) {
        const scope = lastInteraction.scope
        const shouldIgnoreScope =
          scope !== undefined &&
          chatDatasets[scope].entitlements !== undefined &&
          (!enabledSources[scope] || !hasSomeEntitlement(chatDatasets[scope].entitlements ?? []))
        setScope(shouldIgnoreScope ? undefined : lastInteraction.scope)
        setFilesIds(prev => lastInteraction.scopeDocuments?.map(file => file.id) ?? prev)
        setWatchlistId(prev => lastInteraction.scopeWatchlistId ?? prev)
      }
    }
  }, [chat, enabledSources, hasSomeEntitlement, searchParams])

  useEffect(() => {
    let clickedPrompt
    let customerIoScope
    if (context.current && isPromptLibraryContext(context.current)) {
      clickedPrompt = context.current.value
      if (context.current.value.prompt && !scope) {
        setScope(context.current.value.scope)
        customerIoScope = context.current.value.scope
      }

      addPrompt({
        prompt: state.value.prompt,
        customTitle: state.value.label,
        replaceState: true,
        clickedPrompt,
        customerIoScope,
      })
      isComingFromPromptLib.current = true
      context.current = null
    }
  }, [addPrompt, navigate, scope, state])

  useEffect(() => {
    let customerIoScope
    if (context.current && isHomeChatContext(context.current)) {
      setWatchlistId(context.current.value.watchlistId)
      setFilesIds(context.current.value.fileIds)
      setScope(context.current.value.scope)

      // if (context.current.value.prompt && !scope) {
      //   setScope(context.current.value.scope)
      //   customerIoScope = context.current.value.scope
      // }

      addPrompt({
        prompt: state.value.prompt,
        customTitle: state.value.label,
        replaceState: true,
        customerIoScope,
      })
      isComingFromPromptLib.current = true
      context.current = null
    }
  }, [addPrompt, navigate, scope, state])

  useEffect(() => {
    if (context.current && hasWorkflowContext && isChatWorkflowContext(context.current)) {
      addPrompt(generatePromptFromWorkflow(context.current.workflow))
      isComingFromWorkflow.current = true
      context.current = null
    }
  }, [addPrompt, hasWorkflowContext])

  return (
    <ChatDataContext.Provider
      value={{
        chat: {
          id,
          savedChat: chat,
          isLoading,
          isError,
        },
        scope: {
          scope,
          setScope,
        },
        watchlist: {
          watchlistId: watchlistId ?? lastWatchlistId,
          setWatchlistId,
        },
        files: {
          fileIds: realFileIds,
          setFileIds: setFilesIds,
        },
        ui: {
          context,
          currentInteraction,
          setCurrentInteraction,
          isAuditMode,
          setIsAuditMode,
          followUpMap,
          removeFollowUpFromInteraction,
          timingsMap,
          hasContext,
          hasFilesContext: hasFileContext,
          hasWatchlistsContext: hasWatchlistContext,
          isComingFromPromptLibrary: isComingFromPromptLib.current,
          isComingFromWorkflow: isComingFromWorkflow.current,
          isEditable: user?.id === chat?.userId,
        },
        mutations: {
          addPrompt,
          addResponse,
          editLastQuestion,
          regenerateLastResponse,
        },
        helpers: {
          resetChatState,
          setUrlParams,
        },
      }}
    >
      {children}
    </ChatDataContext.Provider>
  )
}

const useChatDataContext = () => {
  const context = useContext(ChatDataContext)

  if (!context) {
    throw new Error("useChatDataContext should be used within a ChatDataProvider")
  }

  return context
}

export { ChatDataProvider, useChatDataContext }
